
import { defineComponent, ref, reactive, computed } from "vue";
import { availableLocales, langSetting } from "@/lang";
import { useRouter, useRoute } from "vue-router";
import { successMessage } from "@/util/message";
import { useStore } from "@/store";
import { useI18n } from "vue-i18n";

export default defineComponent({
    name: "Login",
    setup() {
        const router = useRouter(),
            route = useRoute(),
            store = useStore(),
            { t: $t } = useI18n();

        const loginFormRef = ref(),
            lang = computed<string>(() => store.getters.lang),
            loading = ref(false);

        const changeLang = async (e: string) => {
            await store.dispatch("SET_LANG", e);
            window.location.reload();
            // locale.value = e;
        };

        // 表单数据
        const formData = reactive({
            username: "admin",
            password: "123456",
        });

        // 校验规则
        const rules: object = {
            username: [
                {
                    required: true,
                    message: $t("login.unameReq"),
                    trigger: "blur",
                },
            ],
            password: [
                {
                    required: true,
                    message: $t("login.pwdReq"),
                    trigger: "blur",
                },
                {
                    min: 6,
                    message: $t("login.pwdError"),
                    trigger: "blur",
                },
            ],
        };

        // 登录
        const submit = () => {
            loginFormRef.value.validate((valid: boolean) => {
                if (valid) {
                    loading.value = true;
                    store.dispatch("LOGIN", "a1vb1-da598-dadsa-nahds");
                    const path = route.query.path as string;
                    setTimeout(async () => {
                        // 初始化 collsape 值
                        await store.dispatch("SET_COLLAPSE", false);
                        // 移除tabs
                        await store.dispatch("INIT_TABS");
                        successMessage($t("login.success"));
                        setTimeout(() => {
                            router.replace(path ? path : "/dashboard");
                        }, 1000);
                    }, 1000);
                } else {
                    return false;
                }
            });
        };

        return {
            lang,
            changeLang,
            loginFormRef,
            formData,
            submit,
            loading,
            rules,
            langSetting,
            availableLocales,
        };
    },
});
